export default {
	data() {
		return {
			in_theaters: []
		}
	},
	components: {},
	beforeCreate: function() {
		uni.showLoading({
			title: '加载中'
		});
	},
	beforeUpdate: function() {
		uni.hideLoading()
	},
	onLoad() {
		uni.request({
			url: 'https://douban.uieee.com/v2/movie/in_theaters?apikey=0df993c66c0c636e29ecbb5344252a4a&start=0&count=5',
			success: (res) => {
				for (let i = 0; i < res.data.subjects.length; i++) {
					let pictureResult = res.data.subjects[i].images.small.replace('https://', 'https://images.weserv.nl/?url=');
					res.data.subjects[i].images.small = pictureResult;
				}
				this.in_theaters = res.data.subjects
			},

		})


	},
	methods: {
		enterPic: function(item) {
			uni.navigateTo({
				url: '../detail/detail?info=' + JSON.stringify(item),
				animationType: 'slide-in-right',
				animationDuration: 300,
				success: function() {
					// uni.showLoading({
					// 	title: 'loading...',
					// 	mask: false
					// });
				}
			})
		}
	},
	updated() {
		// console.log(this.in_theaters)
	}
}
